<template>
	<div class="sns">
    <el-row :gutter="20" class="pickers">
      <el-col :sm="6" class="type-selector">
        <el-select v-model="accountValue" @change="changeAccount" clearable placeholder="é€‰æ‹©ç±»åž‹">
          <el-option
            v-for="account in accounts"
            :key="account"
            :label="account"
            :value="account">
          </el-option>
        </el-select>
      </el-col>
      <el-col :sm="6" class="date-selector">
        <el-date-picker
          v-model="monthValue"
          type="month"
          placeholder="é€‰æ‹©æ—¶é—´"
          :picker-options="disableDays"
          @change="changeTime">
        </el-date-picker>
      </el-col>
    </el-row>
    <el-card :body-style="{ padding: '0px' }" v-for="s in sns" :key="s.sns_id">
      <div slot="header" class="clearfix">
        <h3>{{ s.time + " " + s.account }}</h3>
        <router-link :to="{ name: 'SNSDetails', params: {id: s.sns_id} }">
          <el-button style="float: right;" type="text">æ›´å¤š</el-button>
        </router-link>
      </div>
      <div class="block">
        <el-carousel trigger="click">
          <el-carousel-item v-for="pic in s.pics">
            <img :src="pic" class="image">
          </el-carousel-item>
        </el-carousel>
      </div>
    </el-card>
	</div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      monthValue: '',
      disableDays: {
        disabledDate (time) {
          return time.getTime() > Date.now() || time.getTime() < new Date('2016/1/31 23:59:59').getTime()
        }
      },
      sns: [],
      snsBuffer: [],
      accounts: [],
      accountValue: ''
    }
  },
  mounted () {
    axios.get('static/api/sns.json').then(response => {
      var sns = response.data.sns
      this.sns = sns
      this.snsBuffer.push(sns)
      var accounts = []
      for (var i = 0; i < sns.length; i++) {
        if (accounts.indexOf(sns[i].account) === -1) {
          accounts.push(sns[i].account)
        }
      }
      this.accounts = accounts
    })
  },
  methods: {
    changeTime () {
      if (this.snsBuffer.length > 0) {
        if (this.monthValue) {
          // éœ€è¦è€ƒè™‘å¹´ä»½
          var sns = this.snsBuffer[this.snsBuffer.length - 1]
          var year = (this.monthValue.getYear() + '').substring(1, 3)
          var month = this.monthValue.getMonth() + 1 + ''
          var snsToRender = []
          // select corresponding videos from existing items
          for (var i = 0; i < sns.length; i++) {
            if ((sns[i].time.substring(3, 4) === month) && sns[i].time.substring(0, 2) === year) {
              snsToRender.push(sns[i])
            }
          }
          this.snsBuffer.push(snsToRender)
          this.sns = snsToRender
        } else {
          // reset the videos
          this.snsBuffer.pop()
          this.sns = this.snsBuffer[this.snsBuffer.length - 1]
        }
      }
    },
    changeAccount () {
      if (this.accountValue) {
        var sns = this.snsBuffer[this.snsBuffer.length - 1]
        var snsToRender = []
        for (var i = 0; i < sns.length; i++) {
          if (sns[i].account === this.accountValue) {
            snsToRender.push(sns[i])
          }
        }
        this.snsBuffer.push(snsToRender)
        this.sns = snsToRender
      } else {
        // reset the videos
        this.snsBuffer.pop()
        this.sns = this.snsBuffer[this.snsBuffer.length - 1]
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .sns {
    margin-left: 20px;
    padding-top: 20px;
    .type-selector {
      width: 260px;
      .el-select {
        width: 240px;
      }
    }
  }
  @media screen and (max-device-width: 800px) {
    .sns {
      margin: 0 auto;
      .type-selector {
        margin-left: 180px;
      }
    }
  }
</style>



